import {number_format} from '../../components/my-components/formater.js'
    export default {
        props: {
            info: {
                type: Object,
                default: {
                    img: '',
                    label: '',
                    value: 0,
                    index: 0
                }
            },
            index: {
                type: Number,
                default: 0
            },
            pageid: {
                type: String,
                default: 'goods'
            }
        },
        methods: {
            formater(val){
                return number_format(val,2,'.',',')
            },
            clickItem(){
                this.$emit('click',this.info)
            }
        },
    }
